import Vue from 'vue';
import Router from 'vue-router';

import Layout from '@/components/Layout/Layout';
import ListaPan from '@/pages/Panoramicas/ListaPan';
import Notifications from '@/pages/Notifications/Notifications';
import ModeloDenticao from '@/pages/Modelo/ModeloDenticao';
import Anonimizador from '@/pages/Anonimizador/Anonimizador';
import Login from '@/pages/Login/Login';
import ErrorPage from '@/pages/Error/Error';
import ModeloSexo from '@/pages/Modelo/ModeloSexo';
import FaseUm from '@/pages/Modelo/FaseUm';
import FaseDois from '@/pages/Modelo/FaseDois';
import Odontograma from '@/pages/Odontograma/Odontograma';
Vue.use(Router);

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/error',
      name: 'Error',
      component: ErrorPage,
    },
    {
      path: '/app',
      name: 'Layout',
      component: Layout,
      children: [
        {
          path: 'anonimizador',
          name: 'Anonimizador',
          component: Anonimizador,
        },
        {
          path: 'lista_pan',
          name: 'Panoramicas',
          component: ListaPan,
        },
        {
          path: 'notifications',
          name: 'Notifications',
          component: Notifications,
        },
        {
          path: 'components/modelodenticao',
          name: 'ModeloDenticao',
          component: ModeloDenticao,
        },
        {
          path: 'components/modelosexo',
          name: 'ModeloSexo',
          component: ModeloSexo,
        },
        {
          path: 'components/faseum',
          name: 'FaseUm',
          component: FaseUm,
        },
        {
          path: 'components/fasedois',
          name: 'FaseDois',
          component: FaseDois,
        },
        {
          path: 'components/odontograma',
          name: 'Odontograma',
          component: Odontograma,
        },
      ],
    },
    {
      path: '*',
      name: 'Error',
      component: ErrorPage,
    }
  ],
});
