<script>
require('vue-select-image/dist/vue-select-image.css')
import Widget from "@/components/Widget/Widget";
import Dentes from "@/components/Dentes/Dentes";

import { VueTyper } from 'vue-typer'
import { Carousel, Slide } from 'vue-carousel';
import axios from "axios";
import VueSelectImage from 'vue-select-image'
export default {
  name: "Charts",
  components: { Widget,Carousel,Slide,VueSelectImage,VueTyper,Dentes },

  data() {
    return {
      APIurl: 'http://127.0.0.1:8000/panoramica/',
      APIDen: 'http://127.0.0.1:8000/teste-denticao/',
      APIdata: [],
      ultimoEnviado:[],
      image:'',
      panoramicas:[{
        src:'',
        alt:''
      }],
      imageSelected: {
        src: '',
        alt:''
      },

    };
  },
      created() {
    this.initialize();
    this.fileselected(e);
  },

  methods: {
      initialize() {
      axios
      .get(this.APIDen)
      .then((response) => {
        this.APIdata = response.data;
        var i = response.data.length -1
        this.ultimoEnviado = response.data[i];
        console.log('carregando api')
      });
      axios
      .get(this.APIurl)
      .then((response)=>{
        var i = response.data.length -1
        for(var j = 0;j<=i;j++){
        var imagem = response.data[j]
        let newPanoramica={
          src: imagem.panoramica,
          alt: imagem.n_os
        }
        this.panoramicas.push(newPanoramica)
      }
      console.log(this.panoramicas)
      }) 
    },
    fileselected(e){
      const file = e.target.files.item(0);
      this.selectedfile = e.target.files[0];
      console.log(this.selectedfile)
      const reader = new FileReader();
      reader.addEventListener('load', this.imageloaded);
      reader.readAsDataURL(file);
    },
    imageloaded(e){
      this.image = e.target.result;
      this.$refs['identificador'].show()

    },
    envia(){
      this.$refs['identificador'].hide()
      this.$refs['loader'].show()

      var formData = new FormData();
      formData.append('panoramica', this.selectedfile); // add your file to form data
      formData.append('autor',1);
      console.log(formData)
        axios.post(this.APIDen,formData)
        .then((response)=>{   
          this.initialize();
          this.$refs['loader'].hide()
          this.$refs['resultado'].show()
        }).catch((err) => console.log(err.response.data)); 

        },
    onSelectImage: function (data) {
      console.log('fire event onSelectImage: ', data)
      this.imageSelected = data
      const img =  (this.imageSelected.src);
      let filename = (this.imageSelected.alt) + '-pan.jpg'
      const config = { responseType: 'blob' };
      axios.get(img, config).then(response => {
         let blobFile = new File([response.data],filename);   
         this.selectedfile = blobFile
         console.log(this.selectedfile)
         this.envia()    
      });
        
     

    },
      

  },
  

};
</script>

