<script>
require('vue-select-image/dist/vue-select-image.css')
import Widget from "@/components/Widget/Widget";
import 'vue-advanced-cropper/dist/style.css';
import { VueTyper } from 'vue-typer'
import { Carousel, Slide } from 'vue-carousel';
import axios from "axios";
import VueSelectImage from 'vue-select-image'
import { Cropper } from 'vue-advanced-cropper';
export default {
  name: "Charts",
  components: { Widget,Carousel,Slide,VueSelectImage,VueTyper,Cropper },

  data() {
    return {
      APIurl: 'https://papaizapi.machiron.com.br/teste-sexo/',
      APIsexo: 'https://papaizapi.machiron.com.br/teste-sexo/',
      // APIurl: 'http://127.0.0.1:8000/teste-sexo/',
      // APIsexo: 'http://127.0.0.1:8000/teste-sexo/',
      APIdata: [],
      ultimoEnviado:[],
      image:'',
      panoramicas:[{
        src:'',
        alt:''
      }],
      imageSelected: {
        src: '',
        alt:''
      },

    };
  },
      created() {
    this.initialize();
  },

  methods: {
      initialize() {
      axios
      .get(this.APIsexo)
      .then((response) => {
        this.APIdata = response.data;
        var i = response.data.length -1
        this.ultimoEnviado = response.data[i];
        console.log('carregando api')
      });
      axios
      .get(this.APIurl)
      .then((response)=>{
        var i = response.data.length -1
        for(var j = 0;j<=i;j++){
        var imagem = response.data[j]
        let newPanoramica={
          src: imagem.panoramica,
          alt: imagem.n_os
        }
        this.panoramicas.push(newPanoramica)
      }
      console.log(this.panoramicas)
      }) 
    },
    change({ coordinates, canvas }) {
			console.log(coordinates, canvas);
      console.log(coordinates['height'])
		},
    fileselected(e){
      const file = e.target.files.item(0);
      this.selectedfile = e.target.files[0];
      console.log(this.selectedfile)
      const reader = new FileReader();
      reader.addEventListener('load', this.imageloaded);
      reader.readAsDataURL(file);
    },
    imageloaded(e){
      this.image = e.target.result;
      this.$refs['identificador'].show()

    },
    defaultSize() {
			return {
				width: 512,
				height: 512,
			};
    },
    	crop() {

        let data = this.$refs.cropper.getResult();
        let coordinates = (data.coordinates)
		},


    envia(){
      let data = this.$refs.cropper.getResult();
      let coordinates = (data.coordinates)
      this.$refs['loader'].show()
      this.$refs['resultado'].hide()
      var formData = new FormData();
      formData.append('panoramica', this.selectedfile); // add your file to form data
      formData.append('autor',1);
      formData.append('left',coordinates['left'])
      formData.append('height',coordinates['height'])
      formData.append('top',coordinates['top'])
      console.log(formData)
        axios.post(this.APIsexo,formData)
        .then((response)=>{   
          this.initialize();
          this.$refs['loader'].hide()
          this.$refs['resultado'].show()
        }).catch(error=> {
          console.log(error);
          this.$refs['loader'].hide()
          this.$refs['error'].show()

          });
    },


    onSelectImage: function (data) {
      console.log('fire event onSelectImage: ', data)
      this.imageSelected = data
      const img =  (this.imageSelected.src);
      let filename = (this.imageSelected.alt) + '-pan.jpg'
      const config = { responseType: 'blob' };
      axios.get(img, config).then(response => {
         let blobFile = new File([response.data],filename);   
         this.selectedfile = blobFile
         console.log(this.selectedfile)
         this.envia()    
      });
        
     

    },
      

  },
  

};
</script>

