<template>
  <div class="visits-page">
    <h1 class="page-title">Anonimizador &nbsp;
      <small>
        <small>Anonimize suas panormicas</small>
      </small>
    </h1>
    <b-row>
     
     
    </b-row>
    <b-row>
      
      <b-col xl="8" xs="12">
      <Widget
            title="<h6><span class='badge badge-success mr-2'>Upload</span> </h6>"
            refresh close customHeader
      >          
        <div class="pre mb-5">
          <label class='glow-on-hover ml-5' for="arquivo">Escolha o arquivo com a panormica</label>
          <button class='glow-on-hover ml-5 botao-anonimiza' @click=anonimiza() >Anonimizador de laudos panormicos</button>
          <input type="file" name="arquivo" id="arquivo" @change="fileselected">
        </div>
        <div class="pos mb-5">
          <button class='glow-on-hover ml-5 botao-teste' @click=zera() >Escolher outra panoramica</button>           
          <button class='glow-on-hover ml-5 botao-teste' @click=testa() >Testar modelos disponveis</button>           
        </div>
        <div>
          <b-col lg="9">
                
            <img id = "panImg" class = "panoramica ml-5" v-if="image" :src="image" width="400px" />
          </b-col>
        </div>

        <div class = "loader ">       
          <img class="machiron-loader" src="../../assets/icons/machiron.png" width="200px" />
        </div>
        
        </widget>
      </b-col>
    </b-row>

  </div>

</template>

<script>
import Vue from 'vue';
import Widget from '@/components/Widget/Widget';
import axios from "axios";

export default {
  name: 'Visits',
  components: {
    Widget
  },
  data() {
    return {
      APIurl: 'http://127.0.0.1:8000/panoramica/',
      APIdata: [],
      image:'',
      selectedfile:'',
      denticao: '-', 
      sexo:'-',
      prob:'-',
      comp:'-',
    };
  },

  methods: {
    
    fileselected(e){
      const file = e.target.files.item(0);
      this.selectedfile = e.target.files[0];
      console.log(this.selectedfile)
      const reader = new FileReader();
      reader.addEventListener('load', this.imageloaded);
      reader.readAsDataURL(file);
    },
    imageloaded(e){
      this.image = e.target.result;
    },
    anonimiza(){
      this.image = ''
      document.getElementsByClassName("loader")[0].style.visibility = "visible";
      var formData = new FormData();
      formData.append('panoramica', this.selectedfile); // add your file to form data
      formData.append('autor',1);
      console.log(formData)
        axios.post(this.APIurl,formData)
        .then((response)=>{
          console.log(response);
          axios
          .get(this.APIurl)
          .then((response) => {
            var i = response.data.length -1
            this.APIdata = response.data[i];
            this.image = this.APIdata.panoramica
            document.getElementsByClassName("loader")[0].style.visibility = "hidden";
            document.getElementsByClassName("pre")[0].style.visibility = "hidden";
            document.getElementsByClassName("pos")[0].style.visibility = "visible";

          }); 
        }).catch((err) => console.log(err.response.data));
  },
    testa(){
        document.getElementsByClassName("stats-row")[0].style.visibility = "hidden";
        document.getElementsByClassName("loader-table")[0].style.visibility = "visible";
        setTimeout(function() {
          document.getElementsByClassName("loader-table")[0].style.visibility = "hidden";  
          document.getElementsByClassName("stats-row")[0].style.visibility = "visible";
        },3000)
          this.denticao = 'permamente - 97%'
          this.sexo = 'masculino - 85%'
          // this.prob = '89%'
          this.comp = '4% maior'                 
  },
  zera(){
    document.getElementsByClassName("pre")[0].style.visibility = "visible";
    document.getElementsByClassName("pos")[0].style.visibility = "hidden";    
    this.denticao = '-'
    this.sexo = '-'
    this.prob = '-'
    this.comp = '-'
    this.image=''
  }
  }
};

</script>

<style src="./Visits.scss" lang="scss" />

